import React, { useEffect, useRef, useState } from "react";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import BannerBgImg1 from "./stone1.jpg";
import {stange_stones} from "./stangebleau";

const styles = {
  width: "100%",
  height: "calc(100vh - 80px)",
  position: "absolute"
};

const addMarkers = (geojson, map) => {
  geojson.features.forEach(function (marker) {
    // create a DOM element for the marker
    var el = document.createElement('div');
    el.className = 'marker';
    el.style.backgroundImage = `url(${BannerBgImg1})`
    // "require('norkart.jpg');"
    // marker.properties.iconSize.join('/') +
    // '/)';

    //  <img src="norkart.jpg" alt="IMAGE"></img>
    // url("norkart.jpg");
    el.style.width = 15 + 'px';
    el.style.height = 15 + 'px';
    el.title = marker.properties.name
    el.textContent = marker.properties.name
    el.fontSize = 20;
    
     
    el.addEventListener('click', function () {
      window.open(marker.properties.url, '_blank');

        
    });
     
    // add marker to map
    new mapboxgl.Marker(el)
    .setLngLat(marker.geometry.coordinates)
    .addTo(map);
    });
}

const MapboxGLMap = () => {
  const [map, setMap] = useState(null);
  const mapContainer = useRef(null);




  useEffect(() => {
    
    mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_KEY;
    const initializeMap = ({ setMap, mapContainer }) => {
      const map = new mapboxgl.Map({
        container: mapContainer.current,
        style: "mapbox://styles/mapbox/satellite-streets-v11", // stylesheet location
        center: [11.30269, 60.66848],
        zoom: 16
      });

      map.on("load", () => {
        setMap(map);
        map.resize();
        
        addMarkers(stange_stones, map);
      });
    };

    if (!map) initializeMap({ setMap, mapContainer });
  }, [map]);

  

  return <div ref={el => (mapContainer.current = el)} style={styles} />;
};

export default MapboxGLMap;